/* global style start
	============================================================================================ */

body {
	color: $text-color;
  font-family: $text-font;
  font-size: 15px;
	font-weight: 400;
  line-height: 1.667;
}


ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a:hover{
  text-decoration: none;
}

button{
  cursor: pointer;

  &:focus{
    outline: 0 !important;
    box-shadow: none !important;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $title-font;
	color: $title-color;
	line-height: 1.25;
}

h1,
.h1 {
  font-size: 50px;
  // font-weight: 700;
}
h2,
.h2 {
  font-size: 26px;
  
  @media(min-width: 768px){
    font-size: 36px;
  }
}
h3,.h3{
  font-size: 20px;
  // font-weight: 500;
}

