.primary-text{
  color: $primary-color;
}
.bg-gray{
  background-color: #f8f8ff;
}

.pb-90px{
  padding-bottom: 60px;

  @media(min-width: 992px){
    padding-bottom: 90px;
  }
}



.mh-tour{
  max-height: 630px;
}


.section-margin{
  margin: 65px 0;

  @media(min-width: 992px){
    margin: 130px 0;
  }
}

.section-margin-large{
  margin: 65px 0;

  @media(min-width: 992px){
    margin: 140px 0;
  }
}

.section-padding{
  padding: 65px 0;

  @media(min-width: 992px){
    padding: 130px 0;
  }
}

.generic-margin{
  margin-top: 100px;

  @media(min-width: 992px){
    margin-top: 180px;
  }

  @media(min-width: 1200px){
    margin-top: 250px;
  }
}

