/* banner section start
	============================================================================================ */
.hero-banner{
  padding-top: 120px;

  @media(min-width: 992px){
    padding-top: 160px;
  }

  h1{
    text-transform: uppercase;
    color: $primary-color;
    margin-bottom: 15px;

    @media(max-width: 1200px){
      font-size: 40px;
    }

    @media(max-width: 991px){
      font-size: 30px;
    }
  }
}

.hero-banner-sm{
  background: url(../../img/banner/banner.png) left center no-repeat;
  background-size: cover;
  padding-top: 140px;
  padding-bottom: 90px;

  @media(min-width: 992px){
    padding-top: 240px;
    padding-bottom: 140px;
  }

  h1{
    text-transform: uppercase;
    color: $primary-color;

    @media(max-width: 991px){
      font-size: 38px;
    }

    @media(max-width: 768px){
      font-size: 28px;
    }
  }

  p{
    margin-bottom: 0;
  }
}

.hero-banner-sm-content{
  max-width: 400px;
}



.bg-shape{
  position: relative;

  &::before{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 595px;
    height: 660px;
    background: url(../../img/home/bg-shape-left.png) left center no-repeat;
    background-size: cover;
    z-index: -1;

    @media(max-width: 620px){
      display: none;
    }
  }


  &::after{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 755px;
    height: 860px;
    background: url(../../img/home/bg-shape-right.png) left center no-repeat;
    background-size: cover;
    z-index: -1;

    @media(max-width: 620px){
      display: none;
    }
  }
}

.magic-ball{
  position: relative;

  &::before{
    content: "";
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 8px solid $primary-color;
    opacity: .5;
    position: absolute;
    bottom: -68px;
    left: -15px;

    @media(min-width: 768px){
      width: 70px;
      height: 70px;
      left: -35px;
      border: 15px solid $primary-color;
    }
  }

  &::after{
    content: "";
    display: block;
    width: 68px;
    height: 68px;
    border-radius: 50%;
    background-color: rgb(235, 235, 255);
    opacity: .5;
    position: absolute;
    bottom: -263px;
    left: 70px;

    @media(min-width: 768px){
      width: 98px;
      height: 98px;
      left: 165px;
    }
  }
}

.magic-ball-about{

  &::before{
    top: 265px;
    left: -32px;
  }

  &::after{
    top: 65px;
    left: 135px;
  }
}

.magic-ball-testimonial{

  &::before{
    top: 265px;
    left: -32px;
  }

  &::after{
    top: 130px;
    left: 135px;
  }
}

.magic-ball-banner{

  &::before{
    top: 80px;
    left: 130px;
  }

  &::after{
    width: 70px;
    height: 70px;
    top: 270px;
    left: 170px;
  }
}

.magic-ball-sm{


  &::before{
    width: 60px;
    height: 60px;
    top: 280px;
    left: -25px;
  }

  &::after{
    width: 60px;
    height: 60px;
    top: 160px;
    left: 80px;
  }
}


/* banner section end
  ============================================================================================ */



/* breadcrumb section start
  ============================================================================================ */
  .banner-breadcrumb{
    display: inline-block;
    
    .breadcrumb{
      background: transparent;
      padding: 0;
  
      &-item{
        padding: .1rem;
  
        a{
          text-transform: capitalize;
          color: #fff;
        }
  
        &+.breadcrumb-item::before{
          color: #fff;
          padding-left: .2rem;
          padding-right: .4rem;
          // content: "-";
        }
  
        &.active{
          color: #fff;
        }
      }
    }
  }
/* breadcrumb section end
  ============================================================================================ */